// api/firebase.ts
import { initializeApp, getApps } from "firebase/app";
import {
  getFirestore,
  collection,
  getDocs,
  query,
  where,
  limit,
  DocumentData,
  QueryDocumentSnapshot,
} from "firebase/firestore";
import type { SanPham, SuKien } from "./api.type";

export const getFirstImage = (image: SanPham['image']): string | null => {
  if (!image || !Array.isArray(image) || image.length === 0) return null;
  
  const firstItem = image[0];
  if (typeof firstItem === 'string') return firstItem;
  if (typeof firstItem === 'object' && firstItem.url) return firstItem.url;
  
  return null;
};

// Helper function ƒë·ªÉ format image URL
export const formatImageUrl = (imageName: string | null): string => {
  if (!imageName) return '/placeholder.svg?height=400&width=400'
  
  // N·∫øu ƒë√£ l√† URL ƒë·∫ßy ƒë·ªß (c√≥ http/https), return as is
  if (imageName.startsWith('http://') || imageName.startsWith('https://')) {
    return imageName
  }
  
  // N·∫øu ƒë√£ c√≥ slash ƒë·∫ßu, return as is
  if (imageName.startsWith('/')) {
    return imageName
  }
  
  // Th√™m slash ƒë·∫ßu cho relative path
  return `/${imageName}`
};

// Helper function ƒë·ªÉ format gi√° s·∫£n ph·∫©m
export const formatPrice = (gia: number | string | null | undefined): string => {
  if (typeof gia === 'number' && gia > 0) {
    return `${gia.toLocaleString('vi-VN')}ƒë`
  }
  
  if (typeof gia === 'string') {
    const numPrice = Number(gia.replace(/[^\d.-]/g, ''))
    if (!isNaN(numPrice) && numPrice > 0) {
      return `${numPrice.toLocaleString('vi-VN')}ƒë`
    }
  }
  
  return 'Li√™n h·ªá b√°o gi√°'
};

type AnyObject = Record<string, any>;

const firebaseConfig = {
  apiKey: process.env.NEXT_PUBLIC_FIREBASE_API_KEY,
  authDomain: process.env.NEXT_PUBLIC_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.NEXT_PUBLIC_FIREBASE_PROJECT_ID,
  storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.NEXT_PUBLIC_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.NEXT_PUBLIC_FIREBASE_APP_ID,
};

const ensureFirebase = () => {
  if (!getApps().length) initializeApp(firebaseConfig as AnyObject);
  const db = getFirestore();
  return { db };
};

const okResponse = (data: any, status: number = 200) => ({ ok: true, status, data });
const errorResponse = (message: string, status: number = 500) => ({
  ok: false,
  status,
  data: { error: { message } },
});

const normalizeImages = (img: any): string[] => {
  if (!img) return [];

  if (Array.isArray(img)) {
    return img
      .map((it) => {
        if (!it) return null;
        if (typeof it === "string") return it;
        if (typeof it === "object") {
          return it.url ?? it.path ?? it.downloadURL ?? "";
        }
        return null;
      })
      .filter(Boolean) as string[];
  }

  if (typeof img === "string") return [img];
  if (typeof img === "object") {
    const url = img.url ?? img.path ?? "";
    return url ? [url] : [];
  }

  return [];
};

const mapSanPham = (docId: string, p: AnyObject): SanPham => {
  const images = normalizeImages(p.image ?? p.images ?? p.anh);

  const ten = p.TenHoa ?? p.name ?? p.ten ?? "";
  const mota = p.MoTa ?? p.description ?? p.moTa ?? "";

  const rawPrice = p.Gia ?? p.price ?? null;
  const Gia =
    typeof rawPrice === "string" && !Number.isNaN(Number(rawPrice))
      ? Number(rawPrice)
      : rawPrice ?? 0;

  const suKiens: SuKien[] = Array.isArray(p.su_kiens)
    ? p.su_kiens
    : Array.isArray(p.events)
    ? p.events
    : [];

  const sanPham: SanPham = {
    id: docId,
    documentId: docId,
    TenHoa: String(ten),
    MoTa: String(mota),
    Gia,
    slug:
      p.slug ??
      (ten
        ? String(ten)
            .toLowerCase()
            .replace(/\s+/g, "-")
            .replace(/[^a-z0-9\-]/g, "")
        : ""),
    image: images.length ? images : null,
    loai_hoa: p.loai_hoa ?? p["loai_hoa "] ?? p.category ?? p.categoryid ?? undefined,
    su_kiens: suKiens,
    ...p,
  };

  return sanPham;
};

const fetchAll = async (
  colName: string,
  mapper: (id: string, data: AnyObject) => SanPham
): Promise<SanPham[]> => {
  const { db } = ensureFirebase();
  const snap = await getDocs(collection(db, colName));
  return snap.docs.map((d: QueryDocumentSnapshot<DocumentData>) => mapper(d.id, d.data()));
};

const fetchBySlug = async (
  colName: string,
  slug: string,
  mapper: (id: string, data: AnyObject) => SanPham
): Promise<SanPham | null> => {
  const { db } = ensureFirebase();
  const q = query(collection(db, colName), where("slug", "==", slug), limit(1));
  const snap = await getDocs(q);
  if (snap.empty) return null;
  const d = snap.docs[0];
  return mapper(d.id, d.data());
};

const create = () => {
  const getSanPham = async () => {
    try {
      const items = await fetchAll("SanPham", mapSanPham);
      return okResponse(items);
    } catch (e: any) {
      return errorResponse(e?.message || "Failed to load products");
    }
  };

  const getSanPhamBySlug = async (slug: string) => {
    try {
      const item = await fetchBySlug("SanPham", slug, mapSanPham);
      return okResponse(item);
    } catch (e: any) {
      return errorResponse(e?.message || "Failed to load product detail");
    }
  };

  return {
    getSanPham,
    getSanPhamBySlug,
  };
};

const FirebaseApi = create();

export { FirebaseApi, mapSanPham };
